import Vue from "vue";  //vue
import VueRouter from "vue-router"; //vue-router

import myClass from "../page/student/myClass.vue"; //
import myNaotu from "../page/student/myNaotu.vue"; //

import chapters from "../page/student/chapters.vue";

Vue.use(VueRouter);  //usevueRouter

const routes = [
    {
        path: '/class', component: myClass
    },
    { 
       path: '/naotu', component: myNaotu 
    },
    {
        name:'chapters',path:'/chapters',component:chapters
    }
];

const router = new VueRouter({
    mode:'hash',
    routes,
    linkActiveClass: "class_active"  //
});

export default router;

