<template>
  <div class="naotuRoom_wrapper" id="homeWork">
    <!-- start-->
    <el-row class="naotuRoom_header">
      <el-col :span="6" class="header_left">
        <img src="public/js/naotu/images/t_back.png" alt="" @click="naotuBack" v-if="titleFlag">
        <img src="public/js/naotu/images/t_back.png" alt="" @click="naotuChaptersBack" v-else>
      </el-col>
      <el-col :span="12" class="header_middle">
        <div v-if="titleFlag">
          <router-link to="/naotu" tag="a"></router-link>
          <router-link :to="classLink" tag="a"></router-link>
        </div>
        <div v-else>
          <h4 class="text_ellipsis">{{charpterTitle}}</h4>
        </div>
      </el-col>
      <el-col :span="6" class="header_right" v-show="selectFlag">
        <el-select v-model="select" placeholder="" @change="filterChapters">
          <el-option
            v-for="(item,index) in options"
            :key="index"
            :label="item.cs_name"
            :value="item.cs_id"
          ></el-option>
        </el-select>
      </el-col>
    </el-row>
    <!-- end -->
    <!-- tabstart -->
    <router-view :selectList="selectList"></router-view>
    <!-- tabend -->
  </div>
</template>


<script>
import qs from "qs";
export default {
  data() {
    return {
      options: "", //
      select: "", //
      classLink: "/class", ///class
      titleFlag: true, //
      selectList: "", //
      selectFlag: true //
    };
  },
  watch: {
    "$route.path"(newVal) {
      if (newVal === "/class") {
        this.selectFlag = true;
      }
      if (newVal === "/naotu") {
        this.titleFlag = true;
        this.selectFlag = false;
      }
      if (newVal === "/chapters") {
        this.titleFlag = false;
      }
    }
  },
  computed: {
    charpterTitle() {
      //
      if (Object.keys(this.$route.params).length) {
        this.selectFlag = false;
        return this.$route.params.ltId.lt_title; //
      }
    }
  },
  created() {
    this.selectFlag = false; //
    //,
    this.naotuClassList(); //,
    this.routerLink(); //class
  },
  methods: {
    naotuClassList() {
      //
      this.$axios
        .post(
          this.$global.serverSrc + "/api/naotu/naotu_info",
          qs.stringify({
            auth_cookie: this.$NaotuUser.auth_cookie
          })
        )
        .then(res => {
          let { data: classList } = res,
            code = classList.code;
          if (code == 200) {
            this.options = classList.data.dataClass;
            if (this.options.length) {
              this.select = this.options[0].cs_name; //
              this.selectId = this.options[0].cs_id; // id
              this.selectList = this.selectId; //
            }
          } else {
            throw new Error("!");
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    routerLink() {
      //
      this.$router.push({ path: "/class" });
    },
    naotuChaptersBack() {
      //
      this.$router.go(-1);
      this.titleFlag = true;
    },
    naotuBack() {
      //
      this.bus.$emit("drawNaotu");
    },
    filterChapters(item) {
      //
      this.selectList = item;
    }
  }
};
</script>


<style lang="less" scoped>
.naotuRoom_wrapper {
  height: 100%;
  .naotuRoom_header {
    height: 80px;
    background-color: #393f4f;

    .header_left {
      height: 80px;
      line-height: 80px;
      padding-left: 20px;
      cursor: pointer;
      img {
        display: inline-block;
        width: 44px;
        height: 44px;
        vertical-align: middle;
      }
    }

    .header_middle {
      height: 80px;
      line-height: 80px;
      text-align: center;

      a {
        font-family: "PingFang-SC";
        font-weight: bold;
        font-size: 20px;
        color: #ffffff;
        display: inline-block;
        height: 80px;
        width: 150px;
        text-align: center;
        text-decoration: none;
      }

      .class_active {
        color: #fc8383;
        border-bottom: 4px solid #fc8383;
      }

      h4 {
        height: 100%;
        line-height: 80px;
        text-align: center;
        font-family: "PingFang-SC";
        font-weight: bold;
        font-size: 20px;
        color: #ffffff;
      }
    }

    .header_right {
      height: 80px;
      line-height: 80px;
      .el-select {
        height: 40px;
        width: 285px;
        border-radius: 100px;
      }
    }
  }
}

.text_ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>